*{
  box-sizing: border-box;
}

body{
  margin: 0px;
  font-family: 'Poppins', sans-serif;
}

navbar{
  height: 70px;
  display: flex;
  padding: 20px 36px;
  box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
}
navbar .logo{
  max-width: 100px;
}
main{
  padding: 20px;
  display: flex;
  flex-direction: column;
}
.main--photo{
  max-width: 600px;
  align-self: center;
}
main h1{
  margin-bottom: 16px;
}
main p{
  margin-top: 0px;
}
.Cards{ /* COMO CENTRAR ALGO QUE YA DENTRO EST√Å EN FLEX */
  display: flex;
  flex-wrap: nowrap;
  gap: 20px;
  justify-content: center;
}
figure{
  width: 175px;
  font-size: 12px;
  position: relative; /*SOLD OUT PART */
}
figure .soldOut{ /*SOLD OUT PART */
  position: absolute;
  top: 6px;
  left: 6px;
  background-color: white;
  padding: 5px 7px;
  border-radius: 2px;
  font-weight: bold;
}
.figure--img{
  width: 100%;
  height: 250px;
  border-radius: 9px;
  margin-bottom: 9px;
  cursor: pointer;
}
.figure--rank{
  display: flex;
  align-items: center;
}
.rank--star{
  height: 14px;
}
.P-color{
  color: #918E9B;
}
.bold{
  font-weight: bold;
}